.simple-tscroll-fade-enter-active, .simple-tscroll-fade-leave-active {
		-webkit-transition: all .3s;
		transition: all .3s;
}
.simple-tscroll-fade-enter, .simple-tscroll-fade-leave-to {
	    -webkit-transform: translate3d(100%, 0, 0);
	            transform: translate3d(100%, 0, 0);
}
.simple-tscroll {
		background: #fff;
		z-index: 99;
		width: 100%;
		height: 100%;
		position: fixed;
		left: 0;
		top: 0;
		right: 0;
		overflow: hidden;
}
.simple-tscroll .updown {
		width: 100%;
		height: 60px;
		background: #f60;
		text-align: center;
		position: absolute;
		z-index: 1;
		top: 45px;
		left: 0;
}
.simple-tscroll .header {
	    position: absolute;
	    z-index: 2;
	    top: 0;
	    /*left: 0;*/
	    width: 100%;
	    height: 45px;
	    line-height: 45px;
	    background: #B2DFEE;
	    padding: 0;
	    color: #eee;
	    font-size: 20px;
	    text-align: center;
	    font-weight: bold;
}
.simple-tscroll .footer {
	    position: absolute;
	    z-index: 2;
	    bottom: 0;
	    /*left: 0;*/
	    /*right: 0;*/
	    overflow: hidden;
	    width: 100%;
	    height: 48px;
	    background: #B0C4DE;
	    padding: 0;
	    display: -webkit-box;
	    display: -ms-flexbox;
	    display: flex;
	    -webkit-box-orient: horizontal;
	    -webkit-box-direction: normal;
	        -ms-flex-flow: row nowrap;
	            flex-flow: row nowrap;
}
.simple-tscroll .wrapper {
	    position: absolute;
	    z-index: 9999;
	    top: 45px;
	    bottom: 48px;
	    left: 0;
	    width: 100%;
	    background: #ccc;
	    overflow: hidden;
}
.simple-tscroll .scroller {
	    position: absolute;
	    z-index: 1;
	    -webkit-tap-highlight-color: rgba(0,0,0,0);
	    width: 100%;
	    -webkit-transform: translateZ(0);
	    transform: translateZ(0);
	    -webkit-touch-callout: none;
	    -webkit-user-select: none;
	    -moz-user-select: none;
	    -ms-user-select: none;
	    user-select: none;
	    -webkit-text-size-adjust: none;
	    -moz-text-size-adjust: none;
	    -ms-text-size-adjust: none;
	    -o-text-size-adjust: none;
	    text-size-adjust: none;
}
.simple-tscroll .scroller ul {
	    list-style: none;
	    padding: 0;
	    margin: 0;
	    width: 100%;
	    text-align: left;
	    display: block;
}
.simple-tscroll .scroller li {
	    padding: 0 10px;
	    height: 40px;
	    line-height: 40px;
	    border-bottom: 1px solid #ddd;
	    border-top: 1px solid #ddd;
	    background-color: #fff;
	    font-size: 14px;
	    position: absolute;
	    width: 100%;
	    top:0px;
	    left: 0px;
}
.simple-tscroll .loading {
	    text-align: center;
	    line-height: 40px;
	    height: 40px;
	    background: #eee;
}
.simple-tscroll .pulldown {
		text-align: center;
	    line-height: 60px;
	    height: 60px;
	    background: #eee;
	    position: absolute;
	    top: 0;
	    left: 0;
	    right: 0;
}
.simple-tscroll .scroll-to {
		-webkit-box-flex: 1;
		    -ms-flex: 1;
		        flex: 1;
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
		-webkit-box-flex: row;
		    -ms-flex: row nowrap;
		        flex: row nowrap;
}
.simple-tscroll .scroll-to-btn {
        width: 50px;
        height: 100%;
        background: #D8BFD8;
        line-height: 48px;
        color: #fff;
        text-align: center;
}
.simple-tscroll .info {
		-webkit-box-flex: 1;
		    -ms-flex: 1;
		        flex: 1;
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
		-webkit-box-orient: horizontal;
		-webkit-box-direction: reverse;
		    -ms-flex-direction: row-reverse;
		        flex-direction: row-reverse;
}
.simple-tscroll .info-btn {
		width: 50px;
        height: 100%;
        background: #D8BFD8;
        line-height: 48px;
        border-radius: 50%;
        color: #fff;
}
